import React from 'react';
import { Form, Text, TextArea, Radio, RadioGroup, Select, Checkbox } from 'react-form';

// Styles
import './styles.scss';

class Contact extends React.Component {
  render() {
    return (
      <div className="c-contact">
        <div className="wrapper">
          <div className="row center-xs">
            <div className="col-xs-12">
              <h2>Contacto</h2>
              <Form onSubmit={submittedValues => this.setState({ submittedValues })}>
                {formApi => (
                  <form onSubmit={formApi.submitForm} id="form2">
                    <label htmlFor="firstName">Nombre *</label>
                    <Text field="firstName" id="firstName" required />

                    <label htmlFor="lastName">Apellidos *</label>
                    <Text field="lastName" id="lastName" required />

                    <label htmlFor="email">Email *</label>
                    <Text field="email" id="email" type="email" required />

                    <label htmlFor="tel">Tel√©fono *</label>
                    <Text field="tel" id="tel" type="tel" required />

                    <label htmlFor="observations">Observaciones</label>
                    <TextArea field="observations" id="observations" />

                    <button type="submit" className="c-btn -primary">
                      Submit
                    </button>
                  </form>
                )}
              </Form>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Contact;



